body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-nunito: Nunito;

  /* font sizes */
  --font-size-5xs: 8px;
  --font-size-base: 16px;

  /* Colors */
  --color-white: #fff;
  --color-gainsboro: #e5e7eb;
  --color-black: #000;

  /* Gaps */
  --gap-3xs: 10px;

  /* Paddings */
  --padding-3xs: 10px;
  --padding-4xs: 9px;
  --padding-base-6: 15.6px;
  --padding-12xs: 1px;
  --padding-120xl-6: 139.6px;
  --padding-xs: 12px;

  /* border radiuses */
  --br-7xs: 6px;
  --br-45xl: 64px;
}
